import React from 'react';
import './Todo.css';
import Todo from './Todo';
import logo from './logo.svg'

class TodoList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {todoListName:props.name, taskName: '', tasks: [], status: false};
    }

    inputChange = (event) => {
        this.setState({taskName: event.target.value});
    };

    addTask = () => {
        if (this.state.taskName === '') {
            return;
        }
        const id = this.state.tasks.length;
        const name = this.state.taskName;
        this.state.tasks.push({ id, name, done: false });
        this.setState({ taskName: '' });

        // console.log('tasks :', this.state.tasks);
    };

    doneTask = (id) => {
        // console.log('done tasks id',this.state.id);
        // console.log('status',this.state.status);
        const tasks = this.state.tasks;
        tasks.forEach((task)=>{
            if(task.id === id){
                task.done = !task.done;
            }
        });
        // this.tasks.done = !this.tasks.done;
        // console.log('const tasks', tasks);
        this.setState({tasks});

    };

    deleteTask = (id) => {
        // console.log('deleteTask',id);
        const tasks = this.state.tasks.filter((task)=>task.id !== id);
        // console.log('delete task', tasks);
        this.setState({tasks});
    };



    render() {
        return (
            <div className="container">
                <div className="header">
                    <p>
                        {this.state.todoListName}
                    </p>
                    <div className="align">
                        <img className="todo-logo"
                             src={logo}
                             alt="logo"
                             title="Click to add"
                             onClick={() => this.addTask()}
                        />
                        <input type="text"
                               value={this.state.taskName}
                               onChange={this.inputChange}
                        />
                    </div>
                </div>

                <div className="main">
                    <ul style={{ paddingLeft: '10px' }}>
                        {this.state.tasks.map((value, index) => {
                            return (
                                <Todo
                                    key={value.id}
                                    data={value}
                                    // id={value.id}
                                    // value={value.name}
                                    deleteTask={this.deleteTask}
                                    doneTask={this.doneTask}
                                />
                            );
                        })}
                    </ul>
                </div>
            </div>
        );
    }
};


export default TodoList;
